export function getSomeStuffs(token1, cb) {

  fetch('http://138.68.12.208/api/users/userdata?token=' + token1)
    .then(res => res.json())
    .then(json => cb(json))
}

export function getUserFromId(id, cb) {
  fetch('http://138.68.12.208/api/users/userdatafromid?id=' + id)
    .then(res => res.json())
    .then(json => cb(json))
}


